.App {
    font-family: sans-serif;
    text-align: center;
}
html {
    background-color: #1e2022;
}
.sector {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
}
.sector__item {
    padding: 20px;
    margin: 5px;
    background-color: #f0f5f9;
    border-radius: 3px;
    transition: 400ms;
    font-family: 'Courier New', Courier, monospace;
    font-size: 20px;

}
.sector__item:hover {
    cursor: pointer;
    background-color: #c9d6df;
}
.sector__header{
    padding: 20px;
    margin: 40px auto;
    border-radius: 30px;
    font-family: 'Courier New', Courier, monospace;
    background-color: #f0f5f9;
    width: 60%;
}
.clear{
    box-shadow: 0 0 7px
}
.answer{
    font-size: 24px;
    line-height: 38px;
}
.answer__number{
    font-size: 32px;
    font-weight: bold;
}
